<template>
  <div id="app">
    <h1>üìù Todo List</h1>

    <div class="input_area">
      <input 
        v-model="new_task" 
        type="text"
        placeholder="add a new task..." />

      <button @click="add_task">Add</button>
    </div>

    <ul v-if="todos.length">
      <li
          v-for="todo in todos"
          :key="todo.id"
          :class="[todo.done? 'done': '']"
      >

        <input
            type="checkbox"
            :id="'todo-'+ todo.id"
            v-model="todo.done"
        />

        <label :for="'todo-'+ todo.id">{{ todo.text }}</label>

        <a
            @click="remove_task(todo)"
            class="remove"
        >&#215;</a>

      </li>
    </ul>

    <p v-else>Todo list is empty.</p>

  </div>
</template>

<script>
export default {
  name: 'App',

  data: function() {
    return {
      new_task: '',
      todos: [],
    };
  },

  methods: {
    get_task_id: function(){
      return Math.floor(Math.random() * 10000);
    },

    add_task: function() {
      if (this.new_task.trim() !== '') {
          this.todos.push({
              id: this.get_task_id(),
              text: this.new_task.trim(),
              done: false,
          });

          this.new_task = '';
        }
      },

    remove_task:function(todo) {
      let _index = this.todos.indexOf(todo);

            if( _index > -1 ) {
                this.todos.splice(_index, 1);
            }
    }
  }
}
</script>

